    transition: transform .5s ease-in;
    transition: transform .5s ease-in, -webkit-transform .5s ease-in;
}
  
@-webkit-keyframes fade {
    from {
      opacity: 0;
      -webkit-transform: translateY(-10px);
              transform: translateY(-10px);
    }
    to {
      opacity: 1;
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
    }
}
  
@keyframes fade {
    from {
      opacity: 0;
      -webkit-transform: translateY(-10px);
              transform: translateY(-10px);
    }
    to {
      opacity: 1;
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
    }
}


#roadmap {
    padding-bottom: 120px;
    position: relative;
    background: #000;
  }
  
  .container {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
  }
  
  #title {
    padding-top: 40px;
    color: #fff;
    font-size: 4vmin;
    text-align: center;
  }
  
  .roadmap {
    margin: 84px 0 0;
    display: -webkit-flex;
    display: flex;
  }
  
  .block {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 25%;
  }
  
  .block h4 {
    color:rgb(109, 105, 105);
    margin: 0 0 22px;
    font-size: 48px;
  }
  
  .roadmap-line {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    margin: 0 0 22px;
    position: relative;
  }
  
  .roadmap-line-circle {
    height: 9px;
    width: 9px;
    background: #fff;
    border-radius: 20px;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
  }
  
  .block.first .roadmap-line-line {
    height: 3px;
  }
  
  .roadmap-line-line {
    height: 1px;
    width: 100%;
    background: #fff;
  }
  
  .block ul {
    cursor: pointer;
    max-width: 272px;
    margin: 0;
    padding: 20px;
    background: #fff;
    border: 1px solid #fff;
    border-radius: 8px;
    transition: -webkit-transform 0.2s;
    transition: transform 0.2s;
    transition: transform 0.2s, -webkit-transform 0.2s;
  }

  .block ul:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    background: rgb(109, 105, 105);
  }
  
  .block ul li:last-child {
    margin-bottom: 0;
  }
  
  .block ul li {
    margin: 0 0 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: baseline;
            align-items: baseline;
  }
  
  .block ul li p {
    font-weight: 400;
    color: #000;
    font-size: 1em;
    line-height: 1.75;
    margin: 0;
  }
  
  .block ul li p:hover {
    color:#FFF;
  }

  .block.active .roadmap-line-circle {
    background: #000;
      width: 12px;
      height: 12px;
      border: 3px solid #fff;
      top: -3px;
      position: relative;
  }
  
  .block.active .roadmap-line-line {
    position: relative;
    top: -1px;
  }

  
  .roadmap-arrow {
    fill:#fff;
    position: absolute;
    top: -3px;
    right: -1px;
  }



@media only screen and (max-width:850px) {
    .menu-btn {
        display: block;
    }

    .navbar {
        padding: 0;
    }

    .logo {
        position: absolute;
        top: 20px;
        left: 20px;
    }

    .nav-links {
        -webkit-flex-direction: column;
                flex-direction: column;
        width: 100%;
        height: 100vh;
        -webkit-justify-content: center;
                justify-content: center;
        background: #fff;
        margin-top: -900px;
        transition: all 0.5s ease;
    }

    .mobile-menu {
        margin-top: 0px;
    }

    .nav-links li {
        margin: 20px auto;
    }
}

@media only screen and (max-width: 850px) {
    .container {
    padding: 0 32px;
    max-width: 100%;
    z-index: 2;
    position: relative;
    }
    #roadmap {
        padding: 48px 0;
    }
    .roadmap {
        margin: 24px 0 0;
        -webkit-flex-direction: column;
                flex-direction: column;
    }
    .block {
        position: relative;
        width: 100%;
    }
    .roadmap-line {
        position: absolute;
        top: 8px;
        left: 0;
        height: 100%;
        width: 20px;
    }
    .block h4 {
        font-size: 28px;
        margin: 0 0 8px;
        padding-left: 20px;
    }
    .block ul {
        margin-left: 20px;
        max-width: 100%;
        margin-bottom: 20px;
        padding: 0;
    }
    .block ul li {
        margin: 0;
        display: -webkit-flex;
        display: flex;
        -webkit-align-items: baseline;
                align-items: baseline;
        border-bottom: 1px solid #ffffff;
        padding: 20px 36px 20px 20px;
    }
    .block ul li:last-child {
        border: none;
    }
    .roadmap-line-line {
        height: 100%;
        width: 1px;
    }
    .block.first .roadmap-line-line {
        height: 100%;
        width: 3px;
    }
    .roadmap-line-circle {
        position: absolute;
        top: 0;
        left: -3px;
    }
    .block.active .roadmap-line-circle {
        top: 0;
        position: absolute;
        z-index: 2;
    }
    .block.active .roadmap-line-circle {
        top: 0;
        position: absolute;
        left: -6px;
    }
    .roadmap-arrow {
        position: absolute;
        top: auto;
        top: initial;
        right: auto;
        right: initial;
        bottom: -7px;
        -webkit-transform: rotate(90deg);
                transform: rotate(90deg);
        left: -5px;
    }
    .block.last .roadmap-line {
        height: 100%;
    }
}

.column{
    float: left;
    width: 60%;
    padding: 5px;
  }

  .column2{
    float: left;
    width: 30%;
    padding: 5px;
  }


  
  /* Clearfix (clear floats) */
  .row::after {
    content: "";
    clear: both;
    display: table;
  }

  @media screen and (max-width: 500px) {
    .column2 {
      display: none;
    }
    .column {
        width: 100%;
      }
  }

  .card{
    cursor: pointer;
    width: 340px;
    height: 480px;
  }

  .front,.back{
    overflow: hidden;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    position: absolute;
    transition: -webkit-transform .6s linear;
    transition: transform .6s linear;
    transition: transform .6s linear, -webkit-transform .6s linear;
  }


  
  .front{
    -webkit-transform: perspective(600px) rotateY(0deg);
            transform: perspective(600px) rotateY(0deg);
  }
  .back{
    /* background: #f1f1f1; */
    -webkit-transform: perspective(600px) rotateY(180deg);
            transform: perspective(600px) rotateY(180deg);
  }

  .card:hover > .front{
  -webkit-transform: perspective(600px) rotateY(-180deg);
          transform: perspective(600px) rotateY(-180deg);
}
.card:hover > .back{
  -webkit-transform: perspective(600px) rotateY(0deg);
          transform: perspective(600px) rotateY(0deg);
}

